    align-items: center;

}

.new-incident-container .content section {
    width: 100%;
    max-width: 380px;
}

.new-incident-container .content section h1{
    margin: 64px 0 32px;
    font-size: 32px;
} 

.new-incident-container .content section p{
    font-size: 18px;
    color: #737380;
    line-height: 32px;
}

.new-incident-container .content fomr {
    width: 100%;
    max-width: 450px;
